import React from 'react';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import Styles from './India.module.css';

class StateTable extends React.Component{

    render(){

        const { states } = this.props;
        return(
            <>
            <TableRow>
                <TableCell className={Styles.state}>{states.state}</TableCell>
                <TableCell className={Styles.confirmed}>↑ {states.deltaconfirmed}<br/>{states.confirmed}</TableCell>
                <TableCell className={Styles.active}>{states.active}</TableCell>
                <TableCell className={Styles.recovered}>↑ {states.deltarecovered}<br/>{states.recovered}</TableCell>
                <TableCell className={Styles.deaths}>↑ {states.deltadeaths}<br/>{states.deaths}</TableCell>
            </TableRow>
            </>
        )
    }
}

export default StateTable;
