import React, { Component } from 'react';
import axios from 'axios';
import { render } from '@testing-library/react';
import StateTable from './StateTable';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Styles from './India.module.css'; 

class IndiaTable extends React.Component{

    state={
        states:[],
        loading:true,
    }

    async componentDidMount(){
        const res = await axios.get("https://api.covid19india.org/data.json");
        this.setState({states: res.data.statewise});
        this.setState({loading:false});
    }

    render(){
        if(this.state.loading){
            return <h1>loaging...</h1>
        }
        
        return(
            <>
            <div>
            <div className={Styles.header}>
                <p>Covid-<span className={Styles.span}>19</span></p>
            </div>
                <TableContainer className={Styles.container}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell className={Styles.head}>States</TableCell>
                                <TableCell className={Styles.head}>Confirmed</TableCell>
                                <TableCell className={Styles.head}>Active</TableCell>
                                <TableCell className={Styles.head}>Recovered</TableCell>
                                <TableCell className={Styles.head}>Deaths</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {this.state.states.map(States => (
                                <StateTable states={States} key={States.state}/>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
            </>
        )

    }
}

export default IndiaTable;